<template>
    <views class="map">
        <nav-bar
            title="门店分布"
            fixed
            @click-left="onClickLeft">
        </nav-bar>
        <views class="map-body">
            <el-amap-search-box class="map-body-search" :search-option="searchOption" :on-search-result="onSearchResult"></el-amap-search-box>
            <el-amap
                class="amap-box"
                :vid="'shop-detail'"
                :plugin="plugin"
                :center="mapCenter"
                :zoom="14">
                <el-amap-marker v-for="(marker, index) in markers" :key="index" :position="marker"></el-amap-marker>
            </el-amap>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import tools from '@/mixins/tools'
// import api from '@/utils/api'
export default {
    components: {
        NavBar
    },
    mixins: [tools],
    props: {
    },
    data() {
        // const self = this
        return {
            markers: [
                [121.59996, 31.197646],
                [121.40018, 31.197622],
                [121.69991, 31.207649]
            ],
            searchOption: {
                city: '广州',
                citylimit: true
            },
            lng: 0,
            lat: 0,
            // loaded: false,
            mapCenter: [113.400929, 23.055819],
            plugin: [
                {
                    pName: 'OverView',
                    events: {
                        init(instance) {
                            console.log(instance)
                        }
                    }
                },
                {
                    pName: 'Geolocation',
                    events: {
                        init(o) {
                            console.log(o)
                            // o 是高德地图定位插件实例
                            o.getCurrentPosition((status, result) => {
                                if (result && result.position) {
                                    self.lng = result.position.lng
                                    self.lat = result.position.lat
                                    self.mapCenter = [self.lng, self.lat]
                                    // self.loaded = true
                                    self.$nextTick()
                                }
                            })
                        }
                    }
                }
            ]
        }
    },
    methods: {
        onClickLeft() {
            this.back()
        },
        onSearchResult(pois) {
            let latSum = 0
            let lngSum = 0
            if (pois.length > 0) {
                pois.forEach(poi => {
                    const { lng, lat } = poi
                    lngSum += lng
                    latSum += lat
                    this.markers.push([poi.lng, poi.lat])
                })
                const center = {
                    lng: lngSum / pois.length,
                    lat: latSum / pois.length
                }
                this.mapCenter = [center.lng, center.lat]
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
